export function toEnglishTerm(query: string): string {
  // 倒序排列，以便优先替换更长的词语
  const terms = Object.entries(termMap).sort(([key1], [key2]) => -key1.localeCompare(key2));
  const englishQuery = terms.reduce((result, [chineseTerm, englishTerm]) =>
    result.replace(new RegExp(chineseTerm, 'g'), `${englishTerm} `), query);
  return englishQuery.replace(/[^\w\s]/g, ' ');
}

